import {
  MobileUnderlineBigButton,
  MobileUnderlineButton,
} from '../components/underlineButtonMobile';

export default function toolbar(underlineConfig) {
  const underlineLocaleConfig = underlineConfig.locale || {};
  return {
    buttons: {
      mobile: MobileUnderlineButton,
      mobileBig: MobileUnderlineBigButton,
    },
    locale: underlineLocaleConfig,
  };
}
